
.navbar-collapse {
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  text-align: center;
}

.nav-item {
	font-weight: 700;
	font-size: 20px;
	margin-right: 15px;
	color: #fff;
	min-width: 100px;
}

.nav-item.active {
	background-color: rgb(254, 84, 45);
	border-radius: 5px;
}
.nav-item:hover {
	background-color: rgba(254, 84, 45, .5);
	border-radius: 5px;
}

.nav-img {
	width: 170px;
}