html, body {
	height: 100%;
}

.wrapper {
	min-height: 100%;
	width: 100%;
	overflow: hidden;
	padding-bottom: 50px;
}

.container {
	max-width: 1150px;
	padding-left: 15px;
	padding-right: 15px;
	margin-right: auto;
	margin-left: auto;
}

.screen--header {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-attachment: fixed;
}

.screen--nav {
	background-color: rgba(0,0,0,.5);
	padding-top: 20px;
	padding-bottom: 20px;
}

.screen--head {
	padding-top: 70px;
	padding-bottom: 70px;
}

.screen--footer {
	text-align: center;
 	height: 50px;
 	margin-top: -50px;
	background-color: rgb(242,242,242);
	color: rgb(137, 141, 145);

}

.footer-content {
	display: inline-block;
	margin-top: 10px;
}

.footer-link:hover {
	color: rgb(254, 84, 45);
	text-decoration: none;
}