.block-head {
}

.head-title {
	font-size: 40px;
	color: #fff;
	font-weight: bold;
}

.head-sub-text {
	font-weight: bold;
	font-style: italic;
	display: block;
	max-width: 434px;
	margin-top: 10px;
	margin-bottom: 10px;
	font-size: 18px;
	color: #fff;
}

.head-content {
	color: #fff;
}

.head-link {
	margin-top: 10px;
	display: block;
	color: #fff;
	background-color: rgb(254, 84, 45);
	border-radius: 5px;
	max-width: 170px;
	text-align: center;
	padding-top: 10px;
	padding-bottom: 10px;
}

.head-link:hover{
	text-decoration: none;
	color: #fff;
	background-color: rgb(210, 38, 2);
}

.head-logo {
	font-size: 30px;
	text-transform: uppercase;
}